@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

body {
  background-color: #fcfcfc;
  font-family: 'Roboto';
}

p {
  line-height: 150%;
  font-size: 18px;
  /*margin: 0;*/
  margin-bottom: 2.5rem;
}

table,
th,
td {
  border: 1px solid black;
  margin-left: auto;
  margin-right: auto;
  border-collapse: collapse;
}

h1 {
  font-family: 'Roboto Mono';
  text-align: center;
  /*text-transform: uppercase;*/
  font-size: 55px;
  font-weight: normal;
}

h1 span {
  font-weight: normal !important;
}

a {
  color: #1648c6;
  text-decoration: none;
  /*background-image: linear-gradient(120deg, #1d85d2 0%, #dce1ff 100%);*/
  /*background-repeat: no-repeat;*/
  /*background-size: 100% 0.2em;*/
  /*background-position: 0 105%;*/
}

ol,
ul {
  line-height: 150%;
  font-size: 18px;
  margin: 0;
  margin-bottom: 3rem;
}

.chapter_img {
  width: 100% !important;
}

li::marker {
  /*content: counter(list-item)  ". ";*/
  color: black;
  font-size: 1.2rem;
}

h2 {
  overflow: hidden;
  position: relative;
  margin-top: 5rem;
  font-weight: 600 !important;
}
h2 span {
  font-weight: 600 !important;
}

h2::after {
  content: '';
  width: 100%;
  height: 1px;
  background: #1d85d2;
  position: absolute;
  top: 50%;
  margin-left: 1rem;
}

.MuiButton-containedPrimary {
  background-color: #1d85d2 !important;
}

.MuiIconButton-root {
  color: black !important;
}
